.App {
  margin: 0;
  font-family: Arial, sans-serif;
}
.container{
  margin: 0%;
  padding: 0%;
  width: 100vw;
  height: 100vh;
  background-color: rgb(176, 206, 255);
  display: flex;
  justify-content: center;
}
.header{
  background-color: rgb(0, 102, 255);
  width: 100vw;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0px 0px 10px 10px;
}
.body{
  width: 100vw;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
h1{
  font-size:xx-large;
}
.list{
  display: flex;
  align-items:center;
  justify-content: center;
  border: 2px solid mediumorchid;
  border-radius: 25px;
  width: 30vw;
  height: auto;
  background-color: rgb(250, 255, 255);
}
input {
  padding: 10px;
  margin: 30px 0px 10px 0px;
  width: 25vw;
  box-sizing: border-box;
  align-self: center;
}

button {
  padding: 10px 20px;
  margin: 10px 0;
  border-radius: 25px;
  background-color: rgb(69, 137, 255);
}
.RemoveButton{
  background-color: rgb(255, 101, 101);
  border-radius: 15%;
  margin-left: 50px;
}

ul {
  list-style-type: none;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}
li button {
  padding: 5px 10px;
}
